.order {
  
  display: flex;
  flex-direction: column;
  flex: 1;
  &__header {
    &-wrapper {
      padding: 32px 64px;
    }
    &-breadcrumbs {
      padding: 8px 64px;
      border-top: 2px solid #000;
      border-bottom: 2px solid #000;
    }
  }
  &__content {
    padding-left: 64px;
    flex: 1;
    display: flex;
    justify-content: space-between;
    &-main {
      flex: 1;
    }
    &-summary {
      flex: 1;
      max-width: 384px;
      border-left: 2px solid #000;
    }
  }
}
