@import "../../styles/shared.scss";

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    &__logo {
      color: $primary;

      font-style: normal;
      font-weight: bold;
      font-size: 30px;
      line-height: 35px;
    }
    &__city {
      display: flex;
      align-items: center;
      justify-content: space-between;

      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 16px;
      color: $primary-dark;
    }
    &__city p {
      margin-left: 10px;
    }
  }